import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

interface myData {
  email: string,
  status: boolean,
  quote: string
}

interface isLoggedIn {
  status: boolean
}

interface quoteStatus {
  success: boolean
}

interface logoutStatus {
  success: boolean
}

interface location {
  success: boolean,
  city: string
}
@Injectable()
export class UserService {

  constructor(private http: HttpClient) { }

  getData() {
    return this.http.get<myData>('http://localhost:3000/api/userdata/data')
  }

  updateQuote(value) {
    return this.http.post<quoteStatus>('http://localhost:3000/api/userdata/quote', {
      value
    })
  }

  isLoggedIn(): Observable<isLoggedIn> {
    return this.http.get<isLoggedIn>('http://localhost:3000/api/userdata/isloggedin')
  }

  logout() {
    return this.http.get<logoutStatus>('http://localhost:3000/api/userdata/logout')
  }

  getCity(latitude,longitude) {
    return this.http.post<location>('http://localhost:3000/api/userdata/location', {
      latitude,
      longitude
    })
  }  
}